import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	components: {
		uniPopup
	},
	props: {
		type: {
			type: String,
			default: 'number'
		},
		safeAreaInsetBottom: { //是否设置安全区
			type: Boolean,
			default: false
		},
		disableDot: { //数字键盘是否禁止点击.仅type为number生效
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			numKeybordList: [
				[1, 2, 3],
				[4, 5, 6],
				[7, 8, 9],
				[0, '.']
			],
			idCardList: [
				[1, 2, 3],
				[4, 5, 6],
				[7, 8, 9],
				[0, 'X']
			],
			areaList: [
				['京', '沪', '粤', '津', '冀', '豫', '云', '辽', '黑', '湘'],
				['皖', '鲁', '苏', '浙', '赣', '鄂', '桂', '甘', '晋', '陕'],
				['蒙', '吉', '闽', '贵', '渝', '川', '青', '琼', '宁'],
				['藏', '新', '使', '港', '澳', '学']
			],
			EngKeyBoardList: [
				[1, 2, 3, 4, 5, 6, 7, 8, 9, 0],
				['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'],
				['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'],
				['Z', 'X', 'C', 'V', 'B', 'N', 'M']
			],
			active: 1
		};
	},
	methods: {
		open() {
			this.$refs.keyboardPackage.open();
		},
		confirm() {
			this.close();
			this.$emit('onConfirm');
		},
		deleteVal() {
			this.$emit('onDelete');
		},
		input(val) {
			// if (val === '.' && this.disableDot) return;
			this.$emit('onInput', val);
		},
		close() {
			this.$refs.keyboardPackage.close();
		}
	}
}
